import * as React from 'react';
import { MentionBetaNormal } from '@ali/we-design';
import type { QuickInsertConfig } from '@ali/4ever-plugin-quickinsert';
import { insertQuickInsert } from '@ali/4ever-plugin-quickinsert';
import MentionSuggestion from '../components/suggestionForQuickInsert';
import { MentionPluginConfig, Suggestion } from '../types';

export default function createQuickInsertConfig(
  // @ts-ignore
  mentionConfig: MentionPluginConfig<Suggestion>,
) {
  const quickInsert: QuickInsertConfig = {
    onMenuClick: (controller) => controller.run('onAction', insertQuickInsert('quickInsertMention')),
    renderIcon: () => <MentionBetaNormal />,
    renderPanel: (props) => {
      const { text, controller, ...rest } = props;
      return (
        <MentionSuggestion
          {...rest}
          {...mentionConfig}
          controller={controller}
          text={text!.slice(1)}
        />
      );
    },
  };

  return quickInsert;
}
